:root {
  --offset-240: 7.5rem;
  --offset-120: 7.5rem;
  --offset-100: 6.25rem;
  --offset-80: 5rem;
  --offset-70: 4.375rem;
  --offset-60: 3.75rem;
  --offset-56: 3.5rem;
  --offset-55: 3.4375rem;
  --offset-52: 3.25rem;
  --offset-50: 3.125rem;
  --offset-49: 3.0625rem;
  --offset-48: 3rem;
  --offset-42: 2.625rem;
  --offset-40: 2.5rem;
  --offset-38: 2.375rem;
  --offset-36: 2.25rem;
  --offset-32: 2rem;
  --offset-30: 1.875rem;
  --offset-28: 1.75rem;
  --offset-26: 1.625rem;
  --offset-25: 1.5625rem;
  --offset-24: 1.5rem;
  --offset-23: 1.4375rem;
  --offset-22: 1.375rem;
  --offset-20: 1.25rem;
  --offset-18: 1.125rem;
  --offset-16: 1rem;
  --offset-15: 0.9375rem;
  --offset-14: 0.875rem;
  --offset-13: 0.8125rem;
  --offset-12: 0.75rem;
  --offset-10: 0.625rem;
}
@media (max-width: 1024px) {
  :root {
    --offset-240: 7.5rem;
    --offset-100: 2.5rem;
    --offset-80: 2.5rem;
    --offset-70: 2.5rem;
    --offset-56: 1.875rem;
    --offset-50: 1.5rem;
    --offset-49: 1.375rem;
    --offset-42: 2.5rem;
    --offset-36: 2.5rem;
    --offset-26: 1.125rem;
    --offset-25: 3.875rem;
    --offset-23: 0.75rem;
    --offset-22: 0.625rem;
    --offset-20: 1.125rem;
    --offset-13: 0.25rem;
  }
}

.hero {
  padding-top: 3.125rem;
}
@media (max-width: 1024px) {
  .hero {
    padding-top: 1.875rem;
  }
}

.hero-swiper {
  margin-top: 3.125rem;
}
@media (max-width: 767px) {
  .hero-swiper {
    margin-top: 2.5rem;
  }
}

.hero-slide {
  height: 31.25rem;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 200% 200%;
  overflow: hidden;
}
@media (hover: hover) {
  .hero-slide:hover img {
    opacity: 0;
  }
}
.hero-slide img {
  transition: opacity 200ms ease-in-out;
}

.product {
  padding-top: var(--offset-120);
}

.grid-container {
  margin-top: var(--offset-40);
}

.gallery-container {
  display: grid;
  grid-template-columns: repeat(7, minmax(0, 1fr));
  grid-auto-flow: dense;
  gap: 0rem 2.5rem;
  grid-column: 1/8;
  height: 41.25rem;
}
@media (max-width: 1024px) {
  .gallery-container {
    grid-column: 1/-1;
    height: auto;
  }
}

.gallery-thumbs {
  grid-column: 1/3;
}
@media (max-width: 1024px) {
  .gallery-thumbs {
    grid-column: 1/-1;
    width: 100%;
    height: 7.875rem;
    margin-top: 1.25rem;
  }
}

.gallery-main {
  grid-column: 3/-1;
  width: 100%;
  height: 100%;
}
@media (max-width: 1024px) {
  .gallery-main {
    grid-column: 1/-1;
    height: 27.625rem;
  }
}

.gallery-main__thumb {
  overflow: hidden;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 200% 200%;
}
@media (hover: hover) {
  .gallery-main__thumb:hover img {
    opacity: 0;
  }
}
.gallery-main__thumb img {
  transition: opacity 200ms ease-in-out;
}

.product-wrapper {
  grid-column: 8/-1;
}
@media (max-width: 1024px) {
  .product-wrapper {
    grid-column: 1/-1;
    margin-top: 1.875rem;
  }
}
.product-wrapper > span {
  margin-bottom: var(--offset-26);
}
.product-wrapper p:not(.products__price) {
  font-size: 1rem;
  line-height: 1.625rem;
}
@media (max-width: 1024px) {
  .product-wrapper p:not(.products__price) {
    font-size: 0.9375rem;
  }
}
.product-wrapper p:nth-of-type(n + 2) {
  margin-top: var(--offset-28);
}
.product-wrapper p:last-of-type {
  margin-bottom: var(--offset-28);
}

.product-inner {
  max-width: 24.9375rem;
}

.products__item {
  transition: opacity 150ms ease-in-out;
}
.products__item .loader {
  width: 5.0625rem;
  height: 5.0625rem;
}
@media (max-width: 1024px) {
  .products__item .loader {
    width: 3.3125rem;
    height: 3.3125rem;
  }
}
.products__item .loader-wrapper {
  position: absolute;
  top: 19.125rem;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 1024px) {
  .products__item .loader-wrapper {
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
.products__item.loading {
  pointer-events: none;
}
.products__item.loading .loader-wrapper {
  visibility: visible;
}
.products__item.loading .loader {
  animation: l13 1s infinite linear;
}
.products__item.loading .overlay {
  opacity: 0.78;
}

.products__link {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding-bottom: var(--offset-28);
}
@media (hover: hover) {
  .products__link:hover + .products__buy, .products__link:focus + .products__buy {
    background-color: #BDA18C;
  }
  .products__link:hover ~ .products__checkout, .products__link:focus ~ .products__checkout {
    color: #FFF;
    background-color: #3F352C;
  }
}
.products__link:has(.products__size) .products__price {
  margin-top: auto;
}

.products__thumb {
  height: 31.25rem;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 200% 200%;
  overflow: hidden;
}
@media (max-width: 1024px) {
  .products__thumb {
    height: 13rem;
  }
}
@media (hover: hover) {
  .products__thumb:hover .products__image, .products__thumb:focus .products__image {
    opacity: 0;
  }
}

.products__image {
  transition: opacity 200ms ease-in-out;
}

.products__title {
  margin-top: var(--offset-22);
}
@media (max-width: 1024px) {
  .products__title {
    font-size: 1.125rem;
    line-height: 1.37125rem;
  }
}

.products__size {
  margin-top: var(--offset-13);
  margin-bottom: var(--offset-23);
}

.products__price {
  margin-top: 3.625rem;
}
@media (max-width: 1024px) {
  .products__price {
    display: flex;
    justify-content: space-between;
    margin-top: 2.375rem;
  }
}
.products__price > span:first-child {
  margin-right: var(--offset-32);
}
@media (max-width: 1024px) {
  .products__price > span:first-child {
    margin-right: 0;
    font-size: 1rem;
    line-height: 1.21875rem;
  }
}
@media (max-width: 1024px) {
  .products__price > span:last-child {
    font-size: 1.125rem;
    line-height: 1.37125rem;
  }
}

.products__button {
  padding: 0.625rem;
}
@media (max-width: 1024px) {
  .products__button {
    font-size: 0.75rem;
    line-height: 0.914375rem;
  }
}

.products__buy {
  background-color: #3F352C;
  transition: background-color 200ms ease-in-out;
  margin-top: auto;
  color: #FFF;
  background-color: #3F352C;
}
@media (hover: hover) {
  .products__buy:hover, .products__buy:focus {
    background-color: #BDA18C;
  }
}
.products__buy[data-action=delete] {
  background-color: #BDA18C;
}
.products__buy:disabled {
  background-color: #868686;
}

.products__checkout {
  background-color: transparent;
  color: #000;
  transition: background-color 200ms ease-in-out, color 200ms ease-in-out;
  position: relative;
  margin-top: var(--offset-20);
}
@media (hover: hover) {
  .products__checkout:hover, .products__checkout:focus {
    background-color: #3F352C;
  }
}
@media (hover: hover) {
  .products__checkout:hover, .products__checkout:focus {
    color: #FFF;
  }
}
.products__checkout:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0.0625rem solid #000;
}

.no-products {
  margin-top: -2.5rem;
}
@media (max-width: 1024px) {
  .no-products {
    margin-top: 0;
  }
}

.no-products__thumb {
  width: 35rem;
  height: 29.625rem;
}
@media (max-width: 1024px) {
  .no-products__thumb {
    width: 100%;
    height: 18.9375rem;
  }
}

.no-products__title {
  margin-top: var(--offset-52);
  font-size: 2.5rem;
  line-height: 3.0475rem;
  color: #111111;
}
@media (max-width: 1024px) {
  .no-products__title {
    margin-top: var(--offset-52);
    font-size: 2rem;
    line-height: 2.438125rem;
  }
}

.no-products__text {
  max-width: 38.75rem;
  color: #838282;
}
@media (max-width: 1024px) {
  .no-products__text {
    max-width: 20.375rem;
    margin-top: 1.3125rem;
  }
}

.accessories {
  padding-top: var(--offset-240);
  padding-bottom: var(--offset-240);
}

.accessories-title {
  text-align: center;
}
@media (max-width: 1024px) {
  .accessories-title {
    text-align: left;
  }
}

.accessories-wrapper {
  max-width: 66.25rem;
  margin-top: var(--offset-36);
}
@media (max-width: 1024px) {
  .accessories-wrapper {
    max-width: 20.625rem;
  }
}

.accessories-slide {
  box-sizing: border-box;
}

.products__thumb {
  height: 26.5625rem;
}

@media (max-width: 1024px) {
  .products__title {
    margin-top: 1.375rem;
    font-size: 1.25rem;
    line-height: 1.52375rem;
  }
}

@media (max-width: 1024px) {
  .products__price {
    margin-top: 1.125rem;
    justify-content: flex-start;
  }
}
@media (max-width: 1024px) {
  .products__price span:first-child {
    margin-right: 2rem;
    font-size: 1.375rem;
    line-height: 1.67625rem;
  }
}
@media (max-width: 1024px) {
  .products__price span:last-child {
    font-size: 1.5rem;
    line-height: 1.82875rem;
  }
}

@media (max-width: 1024px) {
  .products__button {
    font-size: 1.125rem;
    line-height: 1.37125rem;
  }
}

.accessories-button {
  background-color: #BDA18C;
  transition: background-color 200ms ease-in-out;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  stroke: #FFF;
}
@media (hover: hover) {
  .accessories-button:hover, .accessories-button:focus {
    background-color: #3F352C;
  }
}
@media (max-width: 1024px) {
  .accessories-button {
    top: 22.5rem;
    transform: initial;
  }
}
.accessories-button.prev {
  right: calc(100% + 2.25rem);
}
@media (max-width: 1024px) {
  .accessories-button.prev {
    right: unset;
    left: -1.375rem;
  }
}
.accessories-button.next {
  left: calc(100% + 2.25rem);
}
@media (max-width: 1024px) {
  .accessories-button.next {
    left: unset;
    right: -1.375rem;
  }
}

.accessories-icon {
  width: 3.375rem;
  height: 3.375rem;
}

.accessories-link {
  background-color: transparent;
  color: #000;
  transition: background-color 200ms ease-in-out, color 200ms ease-in-out;
  max-width: 21.6875rem;
  padding: 0.5625rem;
  border: 0.0625rem solid #000;
  margin-top: var(--offset-60);
}
@media (hover: hover) {
  .accessories-link:hover, .accessories-link:focus {
    background-color: #3F352C;
  }
}
@media (hover: hover) {
  .accessories-link:hover, .accessories-link:focus {
    color: #FFF;
  }
}